import React from 'react'
import { Container, Row, Col, Button} from 'react-bootstrap'
import Footer from '../components/Footer'
import { useParams } from 'react-router-dom'
import { useState, useEffect } from 'react'
import data7 from '../choice'
import { addItem } from '../store'
import { useDispatch } from 'react-redux'
import ModalBasic from '../components/ModalBasic'




const Detail = (props) => {
  let { id } = useParams();
  const { soap, hand, incense } = props
  let dispacth = useDispatch();
  let selproduct = soap.find((x)=>x.id==id)
  let choice = data7

  if ((id+1) % 3 ===0){
    choice = choice.hand
  }else if((id+1) % 3 ===1){
    choice = choice.candle
  }else {
    choice = choice.gift
  }

  const [modalOpen, setModalOpen] = useState(false);
  const showModal = () => {
    setModalOpen(true);
    showModal();
  };

  const btnclick = () =>{
    dispacth(addItem({id:selproduct.id,item:selproduct.title,imgUrl:selproduct.imgUrl,price:selproduct.price, amount:1}));
    setModalOpen(true)
   
  }

 
  return (
    

    <Container>
       <div>
            {modalOpen && <ModalBasic setModalOpen={setModalOpen} />}
        </div>
      <Row>
        <Col md={7}>
          <img src={selproduct.imgUrl} width="100%" alt="" />
        </Col>
        <Col md={5} className='coldetail'>

          <h3 className='pt-5 detail'>{selproduct.title}</h3>
          <p className='detail-p'>{selproduct.content}</p>
          <p className='detail-p-2'>{selproduct.price}</p>
          <h6 className='texthand'>{selproduct.text}</h6>
          <button className='cart' onClick={btnclick}></button>

        </Col>

       

      </Row>

 

      <div className="object2">
        <Row>
          <h4 className='text-start'> </h4>
          {
            choice.map((value, i) => 
              <Col md={3} key={i}>
                <img src={value.imgUrl} width="100%" alt="" />
                <h4>{value.title}</h4>
                <h6>{value.content}</h6>
                <h6>{value.price}</h6>
              </Col>
            )
          }
          {/* <Col md={3}>
            <img src={hand[0].imgUrl} width="100%" alt="" />
            <h4>{hand[0].title}</h4>
            <h6>{hand[0].content}</h6>
            <h6>{hand[0].price}</h6>


          </Col>
          <Col md={3}>

            <img src={hand[1].imgUrl} width="100%" alt="" />
            <h4>{hand[1].title}</h4>
            <h6>{hand[1].content}</h6>
            <h6>{hand[1].price}</h6>
          </Col>
          <Col md={3}>
            <img src={hand[2].imgUrl} width="100%" alt="" />
            <h4>{hand[2].title}</h4>
            <h6>{hand[2].content}</h6>
            <h6>{hand[2].price}</h6>

          </Col>
          <Col md={3}>
            <img src={hand[3].imgUrl} width="100%" alt="" />
            <h4>{hand[3].title}</h4>
            <h6>{hand[3].content}</h6>
            <h6>{hand[3].price}</h6>

          </Col> */}
        </Row>
      </div>

      {/* <div className='footers'>
        <table>
          <tbody>
            <tr>
              <th></th>
              <td><a href="#"> </a></td>
            </tr>
            <tr>
              <th></th>
              <td><a href="#"></a></td>
              <td><a href="#">  </a></td>
              <td><a href="#"> </a></td>
              <td><a href="#"></a></td>
              <td><a href="#">  </a></td>
              <td><a href="#">1:1 </a></td>
              <td><a href="#">  </a></td>
            </tr>
            <tr>
              <th></th>
              <td><a href="#">instagram</a></td>
              <td><a href="#">KaKaotalk</a></td>
              <td><a href="#">Weibo</a></td>
              <td><a href="#">Facebook</a></td>
            </tr>
          </tbody>
        </table>

      </div> */}
      

    </Container>



  )
}
export default Detail