import './App.scss';
import { Container, Navbar, Nav, Carousel, Row, Col, Image } from 'react-bootstrap';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom'
import Home from './routes/Home';
import Footer from './components/Footer';
import Intro from './routes/Intro';
import Detail from './routes/Detail';
import data from './soap';
import { useEffect, useState } from 'react';
import Cart from './components/Cart';
import store from './store';




function App() {
  let navigate = useNavigate();
  let [soap, setSoap] = useState(data);
  let [count, setCount] = useState(1);
  
  return (
    <div className="App">

      <Navbar>
        <Container>
          <Navbar.Brand onClick={()=>{navigate('/')}} className='logo'></Navbar.Brand>
         
            <Nav className="me-auto">
              <Nav.Link onClick={() => { navigate('/main') }} className='nav'> </Nav.Link>
              <Nav.Link href="#features" className='nav'></Nav.Link>
              <Nav.Link href="#pricing" className='nav'> </Nav.Link>
              <Nav.Link href="#pricing" className='nav'> </Nav.Link>
            </Nav>
            <Nav>
              <Nav.Link href="#pricing" className='nav bold'>Shop in KR</Nav.Link>
              <Nav.Link onClick={()=>{navigate('/cart')}} className='nav '></Nav.Link>
              <Nav.Link href="#pricing" className='nav'></Nav.Link>
            </Nav>
        
          
        </Container>
        
      </Navbar>
      <Routes>
        
        <Route path='/main' element={<Home soap={soap} setSoap={setSoap} count={count} setCount={setCount}/>} />
        <Route path='/detail/:id' element={<Detail soap = {soap}/>} />
        <Route path='/cart' element={<Cart />}/>
        <Route path='/' element={<Intro/>} />
       
      </Routes>
      <Footer/>
    </div>
  );
}

export default App;
